body > #root > div {
  height: 100vh;
}
.header {
  display: flex;
  flex-direction: column;
  background-image: url('./img/header.jpg');
  background-attachment: fixed;
  background-size:100% 100% ;
  height: 100vh;
  align-items: center;
  justify-content: center;
  min-height: 300px;
}
.container {
  position: absolute;
  height: auto;
  top: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hello-text {
  font-family: "poppins-bold",sans-serif;
  font-weight:bolder;
  color:#cc005f;
  font-size: 2.3rem;
  letter-spacing: 0.3rem;
  text-shadow: 0 0 6px rgba(0,0,0,0.2);
  backdrop-filter: blur(2px);
}
.name-text {
  font-size: 4rem;
  text-shadow: 0 0 6px rgba(0,0,0,0.2);
  font-family: "poppins-medium",sans-serif;
}
.me-text {
  align-self: center;
  font-size: 2rem;
  text-shadow: 0 0 6px rgba(0,0,0,0.2);
}

.more-button {
  margin-top: 1.5rem;
  padding: 1rem;
  border-style: solid;
  border-width: 4px;
  border-color: #6a6a6a;
  border-radius: 5px;
}

.icons {
  position: absolute;
  bottom: 0;
  padding: 1rem;
}

.about-me{
  padding: 7rem;
}

.about{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-title{
  font-family: "poppins-bold";
  font-weight: 900;
  color: #F07;
  font-size: 1.6rem;
  line-height: 1.875;
  margin-bottom: .3rem;
  letter-spacing: .4rem;
}

.about-intro{
  font-family: "poppins-semibold",serif;
  font-size: 3.6rem;
  color: #313131;
  line-height: 1.25;
  margin-bottom: 1.2rem;
}

.about-text {
  font-family: "lora-regular",serif;
  font-size: 2rem;
  line-height: 1.8;
  color: #888;
  max-width: 55ch;
}

.perfil-skills {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: auto auto;
  row-gap: 1rem;
  column-gap: 3rem;
  justify-content: center;
  align-items: center;
}

.title-grid {
  font-family: "poppins-bold",sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: .25rem;
}

.item-title{
  font-family: "poppins-bold",sans-serif;
  font-weight: 500;
  color: #313131;
  letter-spacing: .2rem;
  font-size: 1.5rem;
  line-height: 3rem;
}

.item-text {
  font-family: "poppins-regular",sans-serif;
  color: #888;
  font-size: 1.5rem;
  line-height: 1;
}

.credentials {
  background-color: #ebebeb;
  padding: 7rem;
}
.logo {
  width: 200px;
}

.projects {
  padding: 7rem;
}

.img-container {
  max-width: 600px;
}
.img-long {
  width: 100%;
  height: auto;
}

.img2 {
  width: 20%;
}

.img3 {
  width: 80%;
}

.padd {
  padding: 1.5rem;
}
.img-text {
  font-family: "lora-regular",serif;
  font-size: 2rem;
  line-height: 1.8;
  color: #464545;
  max-width: 55ch;
}
.white {
  color: white;
}